@import "~antd/dist/antd.css";

#header {
  position: relative;
  z-index: 10;
  max-width: 100%;
  background: #fff;
  -webkit-box-shadow: 0 2px 8px #f0f1f2;
  box-shadow: 0 2px 8px #f0f1f2;
}

#header h1 {
  padding-top: 10px;
}

#header-options {
  float: right;
  padding-top: 23px;
}

#content {
  margin-top: 20px;
}

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 576px) {
  .container {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}

.received-order {
  color: rgba(0, 0, 0, 0.3);
}

.pendding-order {
  color: rgba(0, 0, 0, 0.7);
}

#footer {
  margin-top: 20px;
  border-top: 1px solid #f0f1f2;
  text-align: center;
  padding:10px;
  font-size: small;
}